<template>
  <div class="section">
    <section class="container section-spacing">
      <div class="columns is-centered">
        <div class="column is-8">
          <h1 class="title is-1">{{ event.pageRSVPTitle }}</h1>
          <h2 class="subtitle is-4">
            {{ event.pageRSVPSubtitle }}
          </h2>
        </div>
      </div>
    </section>

    <section class="container section-spacing">
      <div class="columns is-centered content">
        <div class="column is-6">
          <Markdown :input="event.pageRSVPContentMarkdown" />
        </div>
      </div>
    </section>

    <section class="container">
      <div class="columns is-centered">
        <div class="column is-6">
          <form @submit.prevent="rsvpForEvent">
            <h2 class="title has-text-poppins is-5 has-red-stripe">
              let's first confirm a few details about you
            </h2>

            <b-field
              label="First Name"
              :type="{ 'is-danger': $v.personAnswers.firstName.$error }"
              :message="
                $v.personAnswers.firstName.$error
                  ? 'Please enter your first name'
                  : ''
              "
            >
              <b-input
                v-model="personAnswers.firstName"
                placeholder="Leslie"
                @blur="$v.personAnswers.firstName.$touch()"
                type="text"
                icon="account"
              >
              </b-input>
            </b-field>

            <b-field
              label="Last Name"
              :type="{ 'is-danger': $v.personAnswers.lastName.$error }"
              :message="
                $v.personAnswers.lastName.$error
                  ? 'Please enter your last name'
                  : ''
              "
            >
              <b-input
                v-model="personAnswers.lastName"
                placeholder="Miles"
                @blur="$v.personAnswers.lastName.$touch()"
                type="text"
                icon="account"
              >
              </b-input>
            </b-field>

            <b-field
              label="Email"
              :type="{
                'is-danger': $v.personAnswers.email.$error || errors.email
              }"
              :message="errors.email ? errors.email : ''"
            >
              <b-input
                v-model="personAnswers.email"
                placeholder="francis@example.com"
                @blur="$v.personAnswers.email.$touch()"
                type="email"
                icon="email"
              >
              </b-input>
            </b-field>

            <b-field
              label="Gender"
              :type="{ 'is-danger': $v.personAnswers.data.gender.$error }"
            >
              <b-select
                v-model.number="personAnswers.data.gender"
                type="text"
                @blur="$v.personAnswers.data.gender.$touch()"
                expanded
              >
                <option selected disabled>Choose One</option>
                <option v-for="option in genders" :value="option" :key="option">
                  {{ option }}
                </option>
              </b-select>
            </b-field>

            <b-field
              label="Year of Birth"
              :type="{ 'is-danger': $v.personAnswers.data.yearOfBirth.$error }"
            >
              <b-select
                v-model.number="personAnswers.data.yearOfBirth"
                type="text"
                @blur="$v.personAnswers.data.gender.$touch()"
                expanded
              >
                <option selected disabled>Choose One</option>
                <option
                  v-for="option in birthYears"
                  :value="option"
                  :key="option"
                >
                  {{ option }}
                </option>
              </b-select>
            </b-field>

            <div class="columns">
              <div class="column">
                <legend class="label">
                  Have you attended TEDxAuckland before?
                </legend>
                <div class="checkbox-row">
                  <b-checkbox
                    v-for="event in previousEvents"
                    :key="event"
                    class="checkbox-column"
                    v-model="personAnswers.data.eventsAttended"
                    :native-value="event"
                  >
                    {{ event }}
                  </b-checkbox>
                </div>
              </div>
            </div>

            <fieldset>
              <legend class="label">
                Do you have any Dietary Requirements?
              </legend>
              <div class="checkbox-row">
                <b-checkbox
                  v-for="requirement in dietaryRequirements"
                  :key="requirement"
                  class="checkbox-column"
                  v-model="personAnswers.data.dietaryRequirements"
                  :native-value="requirement"
                >
                  {{ requirement }}
                </b-checkbox>
              </div>
            </fieldset>

            <b-field
              label="Will you be attending our Community Event?"
              :type="{
                'is-danger': $v.rsvpData.attending.$error
              }"
              class="bottom-space"
              :message="
                $v.rsvpData.attending.$error ? 'Please choose Yes or No' : ''
              "
            >
              <div class="rsvp-radios columns">
                <b-radio
                  v-model="rsvpData.attending"
                  size="is-large"
                  :native-value="true"
                >
                  Yes
                </b-radio>
                <b-radio
                  v-model="rsvpData.attending"
                  size="is-large"
                  :native-value="false"
                >
                  No
                </b-radio>
              </div>
            </b-field>

            <input
              class="button is-medium is-pulled-right is-primary"
              type="submit"
              value="RSVP"
            />
          </form>
        </div>
      </div>
    </section>

    <email-exists-modal
      @closeModal="emailExistsModalActive = false"
      :active="emailExistsModalActive"
      :email="personAnswers.email ? personAnswers.email : ''"
    >
      <p>
        You've already signed up with the email
        <strong>{{ personAnswers.email }}</strong
        >.
      </p>
      <p>
        We've sent an email to that address that will allow you to RSVP and
        update your details.
      </p>
    </email-exists-modal>
  </div>
</template>

<script>
import TicketService from "@/services/TicketService";
import ProfileService from "@/services/ProfileService";
import NProgress from "nprogress";
import axios from "axios";
import { required } from "vuelidate/lib/validators";
import Markdown from "@/components/Markdown";
import EmailExistsModal from "@/components/EmailExistsModal";

export default {
  name: "RSVPForEvent",
  components: {
    Markdown,
    EmailExistsModal
  },
  validations: {
    personAnswers: {
      firstName: { required },
      lastName: { required },
      email: { required },
      data: {
        gender: { required },
        yearOfBirth: { required }
      }
    },
    rsvpData: {
      attending: { required }
    }
  },
  data() {
    return {
      personAnswers: this.person,
      rsvpData: this.rsvp,
      emailExistsModalActive: false,
      errors: {}
    };
  },
  props: {
    event: {
      type: Object,
      required: true
    },
    person: {
      type: Object,
      required: true
    },
    rsvp: {
      type: Object,
      required: true
    }
  },
  methods: {
    rsvpForEvent() {
      this.$v.$touch();
      if (!this.$v.$invalid && !this.errors.email) {
        NProgress.start();

        const createOrUpdateRSVP = (eventSlug, personId, rsvpData) => {
          if (rsvpData.id) {
            return TicketService.updateRSVP(eventSlug, personId, {
              attending: rsvpData.attending
            });
          } else {
            return TicketService.createRSVP(eventSlug, {
              personId: personId,
              attending: rsvpData.attending
            });
          }
        };

        if (this.person.id) {
          axios
            .all([
              ProfileService.updateProfile(this.person.id, {
                person: {
                  firstName: this.personAnswers.firstName,
                  lastName: this.personAnswers.lastName,
                  email: this.personAnswers.email
                },
                profile: this.personAnswers.data
              }),
              createOrUpdateRSVP(this.event.slug, this.person.id, this.rsvpData)
            ])
            .then(
              axios.spread(() => {
                NProgress.done();
                this.$snackbar.open({
                  message: `Successfully RSVP'd`,
                  position: "is-bottom-left"
                });
              })
            );
        } else {
          ProfileService.createProfile({
            person: {
              firstName: this.personAnswers.firstName,
              lastName: this.personAnswers.lastName,
              email: this.personAnswers.email,
              source: `${this.event.slug}-event-rsvp`
            },
            profile: this.personAnswers.data,
            page: {
              name: "event-rsvp",
              eventId: this.event.id
            }
          })
            .then(profileRes => {
              createOrUpdateRSVP(
                this.event.slug,
                profileRes.data.person.id,
                this.rsvpData
              ).then(rsvpRes => {
                NProgress.done();
                this.$snackbar.open({
                  message: `Successfully RSVP'd`,
                  position: "is-bottom-left"
                });
                this.rsvpData = rsvpRes.data.rsvp;
                this.$router.push({
                  name: "rsvp",
                  params: { personId: profileRes.data.person.id }
                });
              });
            })
            .catch(e => {
              NProgress.done();
              this.errors = e.response.data.error;
              this.emailExistsModalActive = true;
            });
        }
      }
    }
  },
  computed: {
    dietaryRequirements() {
      return this.$store.state.dietaryRequirements;
    },
    previousEvents() {
      return this.$store.state.previousEvents;
    },
    birthYears() {
      return this.$store.state.birthYears;
    },
    genders() {
      return this.$store.state.genders;
    }
  }
};
</script>

<style scoped>
.rsvp-radios {
  margin: 1rem;
}
</style>
